header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pcb-board {
  display: flex;
  padding: 10px;
  border-bottom: 4px solid #68A854;
  border-right: 4px solid #68A854;
  border-radius: 10px;
  justify-content: space-between;
  flex-direction: column;
  max-width: 1280px;
  background-color: #75ce5d;
}

.pcb-button {
  display: inline-block;
  margin: 5px 30px 5px 30px;
  border-bottom: 4px solid #888;
  border-right: 4px solid #888;
  padding: 8px;
  background-color: #ddd;
  border-radius: 5px;
}

.pcb-button-input {
  background-color: #222;
  border-bottom: 3px solid #444;
  border-right: 3px solid #444;
  padding: 10px;
  border-radius: 100px;
}

.nav-labels {
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  color: white;
}

.pcb-light {
  display: inline-flex;
  padding: 8px;
  background-color: rgb(150, 0, 0);
  border-radius: 10px 10px 10px 10px;
  border-bottom: 3px solid rgb(100, 0, 0);
  border-right: 3px solid rgb(100, 0, 0);
  margin: 5px;
}

.pcb-light-on {
  box-shadow: 0px 0px 10px red;
  background-color: red;
  border-bottom: 3px solid rgb(200, 0, 0);
  border-right: 3px solid rgb(200, 0, 0);
}

.nav-labels span {
  display: flex;
  justify-items: center;
  align-items: center;
  padding: 0px 15px 0px 15px;
}
