.motherboard {
  padding: 40px;
  border-bottom: 4px solid #68A854;
  border-right: 4px solid #68A854;
  border-radius: 10px;
  justify-content: space-between;
  flex-direction: column;
  max-width: 1600px;
  margin: 20px auto;
  background-color: #75ce5d;
}

.pcb-display {
  background-color: gray;
  border-bottom: 4px solid #555;
  border-right: 4px solid #555;
  border-radius: 10px;
  padding: 5px 20px 20px 20px;
}

.display {
  margin-top: 15px;
  display: flex;
  width: 98%;
  padding: 1%;
  height: 720px;
  background-color: white;
  border-radius: 4px;
}

.pcb-credits {
  position: fixed;
  color: rgba(255,255,255, 0.5);
  bottom: 10px;
  right: 10px;
}
